<template>
  <div>
    <content-title
      title="部门管理"
      tips="此处编辑公司所包含的部门，公式部门涉及人员管理及邮件投递，切勿随意修改."></content-title>
    <content-c>
      <div class="add-dep" v-if="user.roles.some(function(role) {
            return role.id == rolesCpt.admin
          })">
        <div class="add-btn" v-show="addBtn" @click="addDepartment($event)">+新部门</div>
        <p class="add-item" v-show="addItem">
          <input type="text" v-model="depVal">
          <a @click="saveAdd($event)"><img src="../../../assets/icon/hook.png"></a>
        </p>
      </div>
      <ul class="dep-box">
        <li v-for="item in departmentList">
          <p class="dep-item">
            <span>{{item.name}}</span>
            <span v-if="user.roles.some(function(role) {
            return role.id === rolesCpt.admin
          })">
              <i v-if="item.id !== adminDepartmentIdCpt" class="sprites icon-del" @click="delDep(item)"></i>
            <i class="sprites icon-edit" @click="depEdit($event, item)"></i>
            </span>
          </p>
          <p class="edit-item">
            <input type="text" :placeholder="item.name" v-model="editDepName"><a @click="saveEdit($event, item)"><img
            src="../../../assets/icon/hook.png" alt=""></a>
          </p>
        </li>
      </ul>
    </content-c>
  </div>
</template>
<script>
  import contentTitle from '../../componentsBody/contentTitle.vue'
  import contentC from '../../componentsBody/contentC.vue'

  import $ from 'jquery'
  import { mapState, mapActions } from 'vuex'
  import { adminDepartmentId, roles } from '../../../utils/baseConfig'

  export default {
    name: '',
    props: {},
    data () {
      return {
        addBtn: true,
        addItem: false,
        depVal: '',
        editDepName: '', // 编辑部门名称
      }
    },
    computed: {
      ...mapState([
        'user'
      ]),
      ...mapState('department', [
        'departmentList',
        'isDepartmentList',
        'depStatusMsg',
        'delDepartmentList',
        'delDepStatusMsg',
      ]),
      adminDepartmentIdCpt () {
        return adminDepartmentId
      },
      rolesCpt () {
        return roles
      }
    },
    watch: {
      isDepartmentList (newD) {
        if (newD) {
          this.ac_departmentList()
        }
      },
      depStatusMsg (newD) {
        if (newD) {
          this.$message(newD)
        }
      },
      delDepartmentList (newD) {
        if (newD) {
          this.ac_departmentList().then(() => {
            this.$message({
              type: 'success',
              message: '删除成功！'
            })
          })
        }
      },
    },
    methods: {
      ...mapActions('department', [
        'ac_departmentList',
        'ac_departmentAddAndModify',
        'ac_departmentDel',
      ]),
      delDep (item) { // 删除部门
        this.$confirm('此操作将删除该部门及对应的岗位和人员, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.ac_departmentDel({departmentId: item.id})
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      },
      depEdit (e) { // 编辑按钮
        $('.edit-item').hide()
        $('.dep-item').show()
        $(e.target).parents('.dep-item').hide().siblings('.edit-item').show()
      },
      saveEdit (e, item) { // 保存编辑
        if (this.editDepName.length >= 2 && this.editDepName.length <= 20) {
          this.ac_departmentAddAndModify({
            id: item.id,
            name: this.editDepName
          }).then(() => {
            this.editDepName = ''
          })
        } else if (this.editDepName.length !== 0) {
          this.$message('编辑失败！部门长度在2-20之间')
          this.editDepName = ''
        }
        $(e.target).parents('.edit-item').hide().siblings('.dep-item').show()
      },
      addDepartment (e) { // 新增部门按钮
        this.addBtn = false
        this.addItem = true
      },
      saveAdd () { // 确定新增部门
        this.addBtn = true
        this.addItem = false

        if (this.depVal.length >= 2 && this.depVal.length <= 20) {
          this.ac_departmentAddAndModify({name: this.depVal}).then(() => {
            this.depVal = ''
          })
        } else {
          this.$message('新增失败！部门长度在2-20之间')
        }
      }
    },
    components: {
      contentTitle,
      contentC,
    },
    beforeCreate () {},
    created () {
      this.ac_departmentList()
    },
    beforeMount () {},
    mounted () {},
    beforeUpdate () {},
    updated () {},
    beforeDestroy () {},
    destroyed () {}
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../../styles/sprites/sprites.css";

  $li-w: 500px;

  .add-dep {
    .add-item {
      border: 1px solid #FEC71E;
      /*background-color: #DEE8F7;*/
      border-radius: 5px;
      padding: 5px 10px;
      width: $li-w;
      position: relative;
      height: 20px;
      margin: 14px 20px;
      input {
        width: 80%;
        box-sizing: border-box;
        height: 100%;
        position: absolute;
        left: 0px;
        top: 0px;
        border: 0;
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
      }
      a {
        width: 20%;
        height: 100%;
        text-align: center;
        background-color: #FEC71E;
        box-sizing: border-box;
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
        position: absolute;
        right: 0px;
        top: 0px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
    .add-btn {
      width: $li-w;
      padding: 5px 10px;
      border: 1px dashed #B1B9C9;
      font-size: 12px;
      text-align: center;
      color: #ADB4C6;
      margin: 14px 20px;
      border-radius: 5px;
      cursor: pointer;
    }
  }

  .dep-box {
    display: flex;
    flex-wrap: wrap;
    li {
      width: $li-w;
      font-size: 12px;
      margin: 14px 20px;
      border-radius: 5px;
      color: #42454A;
      position: relative;
      i {
        float: right;
        margin-left: 10px;
        margin-right: 10px;
        cursor: pointer;
      }
      &:after {
        content: '';
        display: inline-block;
        clear: both;
      }
      .dep-item {
        border: 1px solid #73ADE9;
        background-color: #DEE8F7;
        border-radius: 5px;
        padding: 5px 10px;
        width: $li-w;
      }
      .edit-item {
        border: 1px solid #FEC71E;
        /*background-color: #DEE8F7;*/
        border-radius: 5px;
        padding: 5px 10px;
        width: $li-w;
        display: none;
        position: relative;
        height: 20px;
        input {
          width: 80%;
          box-sizing: border-box;
          height: 100%;
          position: absolute;
          left: 0px;
          top: 0px;
          border: 0;
          border-top-left-radius: 5px;
          border-bottom-left-radius: 5px;
        }
        a {
          width: 20%;
          height: 100%;
          text-align: center;
          background-color: #FEC71E;
          box-sizing: border-box;
          border-top-right-radius: 5px;
          border-bottom-right-radius: 5px;
          position: absolute;
          right: 0px;
          top: 0px;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }
  }
</style>



